


































































































import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class TransactionDetail extends Vue {
  @Prop() data!: any
  @Prop() dataUser!: any
  getIcon(currency: string): void {
    let icon: any = ''
    if (currency) {
      icon = `icon-${currency.toLowerCase()}`
    }
    return icon
  }
  getLavel(level: string | any): void {
    let name: any = ''
    if (level) {
      name = level.toLowerCase()
    }
    return name
  }
  handleCopyTransaction(string: any): void {
    if (string) {
      let message: any = ''
      const el = document.createElement('input')
      el.value = string
      document.body.appendChild(el)
      el.select()
      document.execCommand('copy')
      document.body.removeChild(el)
      message = this.$t('notify.copy')
      this.$message.success(message)
    }
  }
}
